.Invoice .Invoice__Wrapper {
  margin: auto;
  margin-top: 10rem;
  margin-bottom: 10rem;
  max-width: 350px;
  width: 90%; }

.Invoice .Invoice__Btn {
  margin-bottom: 1rem !important;
  font-weight: 500 !important; }

.Invoice .Invoice__Form input:active,
.Invoice .Invoice__Form input:focus {
  border: 1px solid rgba(34, 36, 38, 0.15) !important; }

.Invoice .Invoice__Form input {
  border-radius: 2px; }

.Invoice .Invoice__Form .Invoice__Form_Message {
  background: #fff !important;
  -webkit-box-shadow: none !important;
          box-shadow: none !important;
  color: #1f1f1f !important; }
